.wrapper {
  width: 600px;
  margin-top: 100px;
  padding-left: 16px;
  padding-bottom: 50px;
}

.list {
  margin: 0;
  padding: 0;
}

.listItem {
  display: flex;
  column-gap: 13px;
}

.finalPrice {
  width: 600px;
  display: flex;
}

.finalPriceWrapper {
  display: flex;
  column-gap: 40px;
  margin-left: auto;
  padding-right: 10px;
  align-items: center;
}

.innerIngredients {
  overflow-y: auto;
  row-gap: 16px;
  display: flex;
  flex-direction: column;
}

.item {
  margin-bottom: 20px;
}

.targetitem {
  min-height: 50px;
  padding: 10px;
  margin-bottom: 20px;
  position: static;
  max-height: 48vh;
}

.content_wrapper{
  overflow-y: auto;
  max-height: 48vh;
  padding-right: 15px;
}

.targetitem.hovered {
  position: relative;
}

.targetitem.hovered::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
  border-radius: 10%;
}

.currency_wrapper {
    display: flex;
    align-items: center;
    column-gap: 5px;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

.alert {
  animation: float 2s linear infinite;
}

.alerts_wrapper {
  margin-bottom: 20px;
  margin-top: 20px;
}